.artwork_item {
  &__type {
    display: inline-block;
    margin: 8px 0; }
  &__title {
    display: inline-block;
    margin: 8px 0; }
  &__image {
    width: 100%;
    max-height: 150px;
    box-shadow: $shadow-2dp;
    margin: 4px 0;
    border: 5px solid $grey;
    box-sizing: border-box;
    cursor: pointer;
    object-fit: cover; } }
